@import "../styles/variables.scss";
@import "../styles/mixins.scss";

.article {
    border: 1px solid $font-color;
    border-radius: 1rem;
    overflow: hidden;
    display: grid;
    height: 500px;

    &__img {
        width: 100%;
        height: 270px;
        object-fit: contain;
    }

    &__texts {
        display: grid;
        padding: 1rem;
        text-align: center;
        gap: 0.5rem;

        @media (max-width: 480px) {
            padding: 0.5rem;
        }
    }

    &__linkPLayAndXbox {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        @include btn(
            linear-gradient(to right, $playAndXbox-color),
            $font-color
        );
        text-decoration: none;
    }

    &__linkPLay {
        @include btn($play-color, $font-color);
        text-decoration: none;
    }

    &__linkXbox {
        @include btn($xbox-color, $font-color);
        text-decoration: none;
    }
}
